@import "../../mixin.scss";
@import "../../vars.scss";

.accountpage {
  margin: 0 auto;
  margin-top: 80px;
  max-width: 600px;

  .settings {
    display: flex;
    flex-direction: column;
    div {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin: 10px 0;
      span {
        font-size: 17px;
        @media (max-width: 700px) {
          font-size: 15px;
        }
      }
      button {
        @include button-submit;
        width: 85px;
        margin: 0;
        height: 30px;
        @media (max-width: 700px) {
          font-size: 14px;
        }
      }
    }
    .managemails{
      outline: none;
      padding: 5px;
      font-size: 15px;
      background: $darkaccentcolor;
      border: 2px solid $darkaccentcolor !important;
      color: #fff;
      font-weight: 700;
      font-size: 17px;
      @include transition;
      cursor: pointer;
      &:hover{
        background: $darkcolor;
      }

    }
    .editnu {
      display: flex;
      align-items: center;
      position: relative;
      button {
        font-size: 16px;
        outline: none;
        position: absolute;
        right: 1px;
        width: 40px;
        height: 28px;
        background: $accentcolor;
        border-radius: 0 15px 15px 0;
        -webkit-border-radius: 0 15px 15px 0;
        -moz-border-radius: 0 15px 15px 0;
        -ms-border-radius: 0 15px 15px 0;
        -o-border-radius: 0 15px 15px 0;
      }
      input {
        height: 30px;
        outline: none;
        border: 1px solid #000;
        @include border-radius;
        padding: 5px 30px 5px 5px;
        &:focus {
          background: rgba($color: #fff, $alpha: 1);
          color: #000;
        }
      }
    }
    .logout {
      cursor: pointer;
      width: 300px;
      height: 35px;
      margin: 15px auto;
      background: #fff;
      font-size: 16px;
      outline: none;
      font-weight: 500;
      @include transition;
      border: 1px solid #000;
      @media (hover: hover) and (pointer: fine) {
        &:hover {
          background: #000000;
          color: #ffffff;
          border: 1px solid #ffffff;
        }
      }
      @media (max-width: 660px) {
        width: 200px;
      }
    }
  }

  .switch {
    position: relative;
    display: inline-block;
    width: 45px;
    height: 28px;
  }

  .switch input {
    opacity: 0;
    width: 0;
    height: 0;
  }

  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: 0.4s;
    transition: 0.4s;
  }

  .slider:before {
    position: absolute;
    content: "";
    height: 20px;
    width: 20px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    -webkit-transition: 0.4s;
    transition: 0.4s;
  }

  input:checked + .slider {
    background-color: $accentcolor;
  }

  input:focus + .slider {
    box-shadow: 0 0 1px $accentcolor;
  }

  input:checked + .slider:before {
    -webkit-transform: translateX(18px);
    -ms-transform: translateX(18px);
    transform: translateX(18px);
    -moz-transform: translateX(18px);
    -o-transform: translateX(18px);
  }

  .slider.round {
    border-radius: 34px;
  }

  .slider.round:before {
    border-radius: 50%;
  }
}
