@import "../../mixin.scss";
@import "../../vars.scss";
.emailslist {
  max-width: 600px;
  width: 100%;
  margin: 0 auto;
  h4 {
    font-size: 22px;
    margin-bottom: 20px;
  }
  .email-card {
    padding: 5px;
    border-bottom: 1px solid rgba(204, 204, 204, 0.5);
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 5px;
    button.del-email {
      @include flexc;
      cursor: pointer;
      outline: none;
      background: none;
      border: none !important;
      height: 20px;
      margin: 3px 0 3px 20px !important;
      @media (hover: hover) and (pointer: fine) {
        &:hover {
          background: none;
          border: none;
        }
      }
    }
    .home-email {
      @include border-radius;
      background: #000;
      padding: 5px;
      width: 30px;
      height: 30px;
      margin: 0 11px 0 28px;
    }
    span {
      font-weight: 500;
    }
  }
  .statistic {
    margin: 0 0 0 auto !important;
  }
}
