@import "../../mixin.scss";
@import "../../vars.scss";

.card-item {
  position: relative;
  padding: 15px;
  height: auto;
  background: #e2e2e2;
  display: flex;
  flex-direction: column;
  @include box-shadow;
  @include transition;
  @include border-radius;
  @media (hover: hover) and (pointer: fine) {
    &:hover {
      box-shadow: none;
    }
  }
  @media (max-width: 660px) {
    min-width: 300px;
    width: 100%;
    max-width: 500px;
  }
  .price-block {
    margin-top: 5px;
    margin-left: auto;
    text-align: end;
  }
  .daysleft {
    padding-top: 10px;
  }
  .btn-block {
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
    button {
      outline: none;
      padding: 5px;
      border: none;
      background: none;
      cursor: pointer;
      img {
        width: 20px;
        height: 20px;
      }
    }
  }
  .info-data {
    .date {
      @media (max-width: 700px) {
        font-size: 13px;
      }
    }
  }
  .data {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-left: 20px;
    .data-top {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
  }
  .main-info {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
  }
  .info {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }
  h4 {
    font-size: 20px;
    text-wrap: nowrap;
    @media (max-width: 700px) {
      font-size: 16px;
    }
  }
  span.period {
    font-family: $font-accent;
    top: 5px;
    right: 15px;
    display: block;
    font-size: 23px;
    font-weight: 700;
    text-transform: uppercase;
    margin-left: 10px;
  }
  &.upc {
    background: #dbc4c4 !important;
  }
  &.upc-over {
    background: #b42c2c;
    border: 1px solid #b42c2c;
    color: #fff;
  }

  .link-button {
    display: flex;
    justify-content: center;
    background: transparent;
    border: none;
    outline: none;
    cursor: pointer;
  }
}
