@import "../../mixin.scss";
@import "../../vars.scss";
.subscriptions {
  @media (max-width: 700px) {
margin-top: 70px;
  }
  .nav {
    display: flex;
    align-items: center;
    margin-bottom: 30px;
    @media (max-width: 660px) {
justify-content: center;    }
    span {
      margin-left: 30px;
      font-size: 20px;
      font-weight: 700;
    }
  }
}
.subs-items {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  row-gap: 20px;
  column-gap: 20px;
  @media (max-width: 1000px) {
    grid-template-columns: 1fr 1fr;
  }
  @media (max-width: 660px) {
    grid-template-columns: 1fr;
    justify-items: center;
  }
}
