@import "../../mixin.scss";
@import "../../vars.scss";
.editpopup {
  @include popup-style;
  .nav {
    @include flexsb;
    h5 {
      font-size: 20px;
    }
    button {
      cursor: pointer;
      outline: none;
      border: none;
      background: none;
      padding: 5px;
    }
  }
  button[type="submit"] {
    @include button-submit;
  }
  form {
    display: flex;
    flex-direction: column;
    margin-top: 10px;
    label {
      @include label-style;
      &.label1 {
        flex-direction: row;
        justify-content: space-between;
      }
      &[for="price"] {
        flex-direction: column;
        justify-content: baseline;
      }
      input[type="checkbox"] {
        @include input-checkbox-style;
      }
      input {
        @include input-style;
      }
    }
  }
}
