@import "../../mixin.scss";
@import "../../vars.scss";
.addemail-popup,
.addcard-popup {
  @include popup-style;
  .nav {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    justify-content: space-between;
    h5 {
      font-size: 20px;
      @media (max-width: 700px) {
        font-size: 18px;
      }
    }
    button {
      outline: none;
      border: transparent;
      cursor: pointer;
      background: none;
      padding: 5px;
      font-size: 16px;
    }
  }
  button[type="submit"] {
    @include button-submit;
    width: 100%;
    @include border-radius;
  }
  label {
    @include label-style;
    &[for="main"],
    &[for="subscr"] {
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      input {
        width: 25px;
        height: 25px;
      }
    }
    input {
      @include input-style;
    }
  }
  select {
    padding: 8px;
    font-size: 16px;
    outline: none;
    width: 100%;
    border: 1px solid #ccc;
    background: #e6e6e6;

    outline: none;
    &[name="emailtype"] {
      margin-top: 5px;
    }
  }
  .btns {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 20px;
    button {
      width: 45%;
      height: 30px;
      outline: none;
      border: 1px solid #fff;
      background: #505050;
      color: #fff;
      @include transition;
      &.active {
        background: #000;
      }
    }
  }
  p.warn {
    text-align: center;
    font-size: 16px;
    margin: 10px;
    color: red;
  }
}
.addemail-popup {
  top: 33%;
}
